* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

:root {
  --white-color: #fffefe;
  --black-color: #050505;
  --blue-color: #00acdc;
}

h1 {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.header img {
  object-fit: cover;
  object-position: center;
  width: 40px;
  height: 40px;
}

.nav {
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav li {
  margin-right: 20px;
}

.nav li:last-child {
  margin-right: 0;
}

.link {
  color: var(--black-color);
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover, .is-active {
  color: var(--blue-color);
}

.link.is-active:after {
  content: "";
  background: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
  left: 0;
}

.link[data-qa="hover"]:hover {
  color: var(--blue-color);
}
